/* Sección principal */
.hero-section {
  position: relative;
  height: 150vh; /* Altura suficiente para permitir scroll */
  overflow: hidden;
  display: flex;
  align-items: center; /* Centra verticalmente el contenido */
  justify-content: center; /* Centra horizontalmente el contenido */
  background-color: black; /* Fondo para el section */
}

/* Contenedor del video */
.video-container {
  position: relative; /* Relativo para estar dentro del section */
  width: 60vw; /* Tamaño inicial del video */
  height: 34vw; /* Proporción inicial (16:9) */
  display: flex;
  align-items: center; /* Centra verticalmente el video */
  justify-content: center; /* Centra horizontalmente el video */
  pointer-events: none; /* Evita interacción con el video */
  transform-origin: center center; /* Escala desde el centro */
}

/* Video */
.video-bg-element {
  width: 100%;
  height: 100%;
  object-fit: cover; /* Ajusta el video al contenedor */
  border-radius: 8px; /* Opcional: bordes redondeados */
}

/* Superposición del texto */
.video-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  z-index: 1; /* Aparece encima del video */
}

.video-overlay h1 {
  font-size: 2.5rem;
  margin: 0 0 10px 0;
}

.video-overlay p {
  font-size: 1.2rem;
  margin: 0;
}

/* Responsividad */
@media (max-width: 768px) {
  .video-container {
    width: 90vw; /* Ajusta el tamaño para pantallas pequeñas */
    height: 50vw;
  }

  .video-overlay h1 {
    font-size: 2rem;
  }

  .video-overlay p {
    font-size: 1rem;
  }
}
